/* Base Styles */
body {
  margin: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f5f5f5;
  color: #333;
}

.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.app-header {
  background-color: #2c3e50;
  color: white;
  padding: 1rem;
  box-shadow: 0 2px 5px rgba(0,0,0,0.2);
}

.app-header h1 {
  margin: 0;
  text-align: center;
}

.nav-tabs {
  display: flex;
  overflow-x: auto;
  padding: 0.5rem 0;
  gap: 0.5rem;
}

.nav-btn {
  background: #34495e;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  white-space: nowrap;
}

.nav-btn.active {
  background: #3498db;
  font-weight: bold;
}

.main-content {
  flex: 1;
  padding: 1rem;
  position: relative;
}

/* Form Styles */
.tab-content {
  background: white;
  padding: 1.5rem;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  margin-bottom: 1rem;
}

.form-group {
  margin-bottom: 1rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 600;
}

.form-group input,
.form-group select,
.form-group textarea {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-sizing: border-box;
}

.form-group textarea {
  min-height: 100px;
  resize: vertical;
}

.upload-area {
  border: 2px dashed #ddd;
  padding: 1rem;
  text-align: center;
  border-radius: 4px;
}

.file-preview {
  margin-top: 0.5rem;
  padding: 0.5rem;
  background: #f0f0f0;
  border-radius: 4px;
}

/* Button Styles */
button {
  background-color: #3498db;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.2s;
}

button:hover {
  background-color: #2980b9;
}

button.primary {
  background-color: #2ecc71;
}

button.primary:hover {
  background-color: #27ae60;
}

.logout-btn {
  background-color: #e74c3c;
}

.logout-btn:hover {
  background-color: #c0392b;
}

.action-buttons {
  display: flex;
  gap: 0.5rem;
  margin-top: 1rem;
}

/* Loading Overlay */
.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,0.5);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.spinner {
  border: 4px solid rgba(255,255,255,0.3);
  border-radius: 50%;
  border-top: 4px solid #3498db;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Log Styles */
.log-section {
  margin-bottom: 1.5rem;
}

.log-container {
  height: 300px;
  overflow-y: auto;
  background-color: #000;
  color: #0f0;
  padding: 1rem;
  font-family: monospace;
  border-radius: 4px;
}

.log-entry {
  margin-bottom: 0.2rem;
  line-height: 1.3;
}

.log-empty {
  color: #888;
  font-style: italic;
}

.serial-monitor {
  background-color: #f8f8f8;
  color: #333;
}

/* Board Selection */
.board-selection {
  display: flex;
  gap: 0.5rem;
}

.board-info {
  margin-top: 1.5rem;
  padding: 1rem;
  background: #f0f8ff;
  border-radius: 4px;
}

.board-tips {
  margin-top: 1rem;
  padding: 0.5rem;
  background: #e6f7ff;
  border-radius: 4px;
}

.preference-info {
  color: #27ae60;
  font-style: italic;
}

/* Controls Grid */
.controls-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1rem;
}

.control-card {
  background: white;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 1rem;
  box-shadow: 0 2px 3px rgba(0,0,0,0.1);
}

.control-card h3 {
  margin-top: 0;
  text-align: center;
}

.control-actions {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
}

/* Report Styles */
.report-container {
  margin-top: 1rem;
}

.report-textarea {
  width: 100%;
  min-height: 300px;
  padding: 1rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-family: monospace;
}

.report-actions {
  display: flex;
  gap: 0.5rem;
  margin-top: 0.5rem;
}

/* Camera Styles */
.camera-view {
  margin-top: 1rem;
}

.camera-feed {
  width: 100%;
  max-height: 500px;
  object-fit: contain;
  background: #000;
  border-radius: 4px;
}

.camera-placeholder {
  padding: 2rem;
  text-align: center;
  background: #f0f0f0;
  border-radius: 4px;
}

.camera-error {
  padding: 1rem;
  background: #ffebee;
  color: #c62828;
  border-radius: 4px;
  text-align: center;
}

.camera-status {
  margin-top: 0.5rem;
  font-style: italic;
  color: #3498db;
}

.stream-info {
  margin-top: 0.5rem;
  font-size: 0.9rem;
  color: #666;
}
/* Job status indicators */
.job-item {
  padding: 8px;
  margin: 4px 0;
  border-radius: 4px;
}

.job-item.queued {
  background: #FFF3CD;
}

.job-item.processing {
  background: #CCE5FF;
}

.job-item.success {
  background: #D4EDDA;
}

.job-item.error {
  background: #F8D7DA;
}

/* Admin Section */
.admin-section {
  background: #2c3e50;
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 1rem;
  color: white;
}

.admin-section h3 {
  margin-top: 0;
  color: white;
}

.admin-section .form-group label {
  color: white;
}

.camera-info {
  background: #f8f9fa;
  padding: 1rem;
  border-radius: 8px;
  margin-top: 1rem;
}

/* Footer */
.app-footer {
  background: #2c3e50;
  color: white;
  text-align: center;
  padding: 1rem;
  margin-top: auto;
}

/* Responsive */
@media (max-width: 768px) {
  .nav-tabs {
    flex-wrap: wrap;
  }

  .controls-grid {
    grid-template-columns: 1fr;
  }
}
/* === Backend Tunnel URL card === */
.backend-url-card {
  background: #1f2a38;
  color: #fff;
  border: 1px solid #3a4a5e;
  border-radius: 8px;
  padding: 1rem;
  margin-top: 1rem;
}
.backend-url-card h3 { margin: 0 0 .75rem; font-size: 1.1rem; color: #e8f4ff; }
.backend-url-row { display: flex; gap: .5rem; align-items: center; margin-bottom: .5rem; }
.backend-url-row input[type="text"] {
  flex: 1; min-width: 240px; padding: .6rem .7rem; border-radius: 6px;
  border: 1px solid #4a5c74; background: #0f1822; color: #e8f4ff; outline: none;
}
.backend-url-row input::placeholder { color: #9fb3c8; }
.backend-url-actions { display: flex; gap: .5rem; margin-top: .25rem; }
.backend-url-actions button { background: #2ecc71; }
.backend-url-actions button.secondary { background: #6c757d; }
.backend-url-actions button:hover { filter: brightness(0.95); }
.backend-url-current { margin-top: .5rem; font-size: .9rem; color: #b8d3ff; word-break: break-all; }
.env-hint { margin-top: .35rem; font-size: .8rem; color: #ffdf91; opacity: .9; }

/* improve inputs in dark admin section */
.admin-section .form-group input,
.admin-section .form-group select,
.admin-section .form-group textarea {
  background: #0f1822;
  color: #e8f4ff;
  border-color: #4a5c74;
}
